-1.jpg"}})],1),_c('figure',[_c('Tablet',{attrs:{"alt":"Yodle Restaurants app review filled","img":"yodlerestaurants/yodlerestaurants-ipad-review-2.jpg"}})],1),_vm._m(2),_c('p',[_vm._v("The secondary screens are focused on administrative interactions—specifically logging into the appropriate restaurant (many restaurants have multiple locations they can track using a single login). The other specific task the app does is allows staff to track promotions.")]),_c('p',[_vm._v("Typically, customers receive promotional emails offering discounts. The app doesn’t tie into POS systems, so having a way to track which promotions have actually been activated and pushing those results to the dashboard was crucial.")]),_c('figure',[_c('Tablet',{attrs:{"alt":"Yodle Restaurants app settings","img":"yodlerestaurants/yodlerestaurants-ipad-settings.jpg"}})],1),_c('figure',[_c('Tablet',{attrs:{"alt":"Yodle Restaurants coupon validation","img":"yodlerestaurants/yodlerestaurants-ipad-coupons.jpg"}})],1),_vm._m(3),_c('h2',[_vm._v("Client Dashboard")]),_c('p',[_vm._v("The client dashboard was one of the most intricate parts of the project. Two variations of the dashboard were created—one for restaurant managers (the ‘blue’ dashboard) to interact with reviews—and a second variation (the ‘gray’ dashboard) used by Yodle to manage individual restaurant accounts. They are visually similar but serve very different purposes.")]),_c('figure',[_c('Browser',{attrs:{"alt":"Yodle Restaurants customer reviews overview","img":"yodlerestaurants/yodlerestaurants-reviews.jpg"}})],1),_vm._m(4),_c('p',[_vm._v("The client dashboard is predominantly about reviews, so we made it easy to sort, filter and interact with reviews straight from the overview page. Often managers will try to reach out to customers who left a negative review and find out more information. Customers can continue communicating with managers through email and the whole conversation gets pushed to the dashboard for history.")]),_c('figure',{